<template>
  <div class="main01" >
  <!-- 页面请求的数据整体展示部分，将每一条消息 -->
  <div v-if="Topices.length">
  <template v-for="(item,index) in Topices">
    <ArticleList :key="index" :item='item'/>
  </template>
  </div>

  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import ArticleList from './ArticleList.vue';
export default {
  components: { ArticleList },
  name: "Main",
  created() { 
    // console.log("函数执行");
   
    this.getTopice(this.path)
    //  console.log("函数执行完成");-
    //   console.log(this.Topices);
    
  },
  
  watch: {
    // 监听动态路由中的path，当路径发生改变时，刷新我们的vuex中的数据
  //  async path(newValue, oldValue) {
  //     // console.log("Url改变了",newValue);
  //     //    console.log("函数执行");
  //       await this.getTopice(newValue)
  //       // console.log(this.$route.params);
  //   //     console.log("函数执行完成");-
  //   //     console.log(this.Topices);
  //   },
    path:{
     async handler(newValue){
        const tab=newValue?newValue:'all'
         await this.getTopice(tab)
      },
      immediate: true
    }
  },
  
  computed: {
    // 将路由中的params提取出来。
    path() {
      return this.$route.params.mainpath;
    },
    ...mapState({
      Topices: (state) => {
      
      return  state.Topices.topice;  
      },
    }),
  },
  methods: {
    ...mapActions(['getTopice'])
  },
  
};
</script>
<style lang='less'>
.main01 {
  width: 100%;
  min-height: 500px;
}
</style>