<template>
  <div class="article-list">
    <!-- 请求来的每一条数据详细展示组件。 -->
     <div class="image"> <img :src="imgurl" :title="item.author.loginname" @click="showAuthor"></div>
     <div class="count" v-show='item.visit_count'><span title="回复数">{{item.reply_count}}</span>/<span title="点击数">{{item.visit_count}}</span></div>
    <div class="title"><span v-if="this.$route.params.mainpath===undefined||this.$route.params.mainpath==='all'||this.$route.params.mainpath==='ask'"  :class="{btn,'tab':this.item.tab==='ask'||this.item.top}">{{btn}}</span><span @click="showContext" :title="item.title">{{ item.title }}</span></div>
    <div class="time">{{item.last_reply_at | formatDate}}</div>
  </div>
</template>                                                

<script>
export default {
  name: "ArticleList",
  data() {
    return {
      imgurl: this.item.author.avatar_url,
    };
  },
  computed: {
    btn(){
    return  this.item.good?'招聘':this.item.top?'置顶':this.item.tab==='share'?'共享':this.item.tab==='job'?'问答':this.item.tab==='ask'?'精华':''
      
    }
  },
  props: {
    item: {
      type: Object,
      default: null,
    },
  },
  methods: {
    // 在该方法下跳转到context页面
    showContext() {
    //跳转至context页面
    const id=this.item.id
    console.log("开始跳转");
     this.$router.push( {path:'/context',query:{id:id}})
    //  {path:`/context`,query:{id:id}}
    //
    },
    showAuthor(){
      const loginname=this.item.author.loginname
      this.$router.push({path:'/authorpage',query:{loginname:loginname}})
    }
  },
};
</script>

<style lang='less'>
.article-list {
  width: 100%;
  height: 45px;
  line-height: 45px;
  display: flex;
  border-bottom: 1px solid #231e3f;
  // justify-content: center;
  .image{
    width: 5%;
    margin: 0 auto;
    img{
      margin-top: 10px;
        // align-items: center;
        width: 26px;
        height: 26px;
        cursor: pointer;
      }
  }
  .count{
    width: 10%;
    margin: 0 auto;
  }
  .title{

    cursor: pointer;
    width: 60%;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    word-wrap: break-word;
    white-space: normal;
    overflow: hidden;
    .btn{
      display: inline-block;
      // padding-right: 5px;
      margin-right: 5px;
      border: 1px solid rgb(16, 4, 37);
      height: 25px;
      line-height: 25px;
      width: 40px;
      text-align: center;
      font-size: 12px;
      border-radius: 5px;
    }
    .btn.tab{
      background-color: rgb(34, 196, 74);
    }
  }
 .time{
   width: 25%;
   text-align: right;
 }
}
</style>