<template>
  <div class="text-editing">
    <NavgationList>
      <template v-slot:header>
        <div class="author-page-nav">
          <router-link to="/">主页/</router-link><span>发布话题</span>
        </div>
      </template>
      <template v-slot:listMain>
        <el-row>
          <el-form
            ref="form"
            :model="ruleForm"
            :rules="rules"
            label-width="80px"
          >
            <el-form-item prop="title" label="输入标题">
              <el-input
                v-model.trim="ruleForm.title"
                placeholder="标题字数10字以上"
              ></el-input>
            </el-form-item>
            <el-form-item label="选择板块" prop="tab">
              <el-select v-model="ruleForm.tab" placeholder="请选择">
                <el-option label="分享" value="share"></el-option>
                <el-option label="问答" value="ask"></el-option>
                <el-option label="招聘" value="job"></el-option>
                <el-option label="测试." value="dev"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label='正文部分' prop="content">
        <editor
          api-key="9mieih2cqsubf30b1omff3w107ojvjfvzhmb7haq4giiy754"
          v-model="ruleForm.content"
          model-events="change keydown blur focus paste"
          :init="{
            height: 500,
            menubar: true,
            language: 'zh_CN',
            branding: false,
            plugins: [
              'advlist autolink lists link image charmap print preview anchor',
              'searchreplace visualblocks code fullscreen',
              'insertdatetime media table paste code help wordcount',
            ],
            toolbar:
              'code undo redo restoredraft | cut copy paste pastetext | forecolor backcolor bold italic underline strikethrough link anchor | alignleft aligncenter alignright alignjustify outdent indent | \
          styleselect formatselect fontselect fontsizeselect | bullist numlist | blockquote subscript superscript removeformat | \
          table image media charmap emoticons hr pagebreak insertdatetime print preview | fullscreen | bdmap indent2em lineheight formatpainter axupimgs',
          }"
        />
            </el-form-item>
              <el-form-item>
    <el-button type="primary" @click="submitForm('form')">立刻发布</el-button>
   
  </el-form-item>
          </el-form>
        </el-row>

      </template>
    </NavgationList>
  </div>
</template>

<script>
import Editor from "@tinymce/tinymce-vue";
import NavgationList from "../components/NavgationList";

export default {
  name: "TextEditing",
  components: {
    editor: Editor,
    NavgationList,
  },
  data() {
    return {
      ruleForm: {
        tab: "",
        title: "",
        content: "",
      },

      rules: {
        title: [
          { required: true, message: "请输入标题", trigger: "blur" },
          { min: 10, message: "长度不能少于10个字符", trigger: "blur" },
        ],
        tab: [{ required: true, message: "请选择发布板块", trigger: "change" }],
        content:[{ required: true, message: "请输入正文", trigger: "blur" },
          { min: 5, message: "长度不能少于5个字符", trigger: "blur" },]
      },
    };
  },
  methods: {
submitForm(form) {
        this.$refs[form].validate((valid) => {
          if (valid) {
            console.log("到目前为止没有问题");
          //  在这里发送请求上传文章。
          const token=sessionStorage.getItem("isLogin")
          this.$axios.post("/topics",{accesstoken:token,...this.ruleForm})
          this.$router.push('/all')
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
  },
};
</script>

<style lang='less'>
.text-editing {
  width: 1000px;
  margin-left: 150px;
  .author-page-nav {
    padding-left: 20px;
    width: 100%;
    height: 45px;
    line-height: 45px;
    background-color: #918e8e;
    border-bottom: 1px solid #fff;
    a {
      font-weight: 600;
      color: rgb(22, 9, 92);
    }
  }
  .el-input {
    margin-top: 10px;
  }
  .el-form-item__label {
    color: #fff;
  }
}
</style>