<template>
  <div class="author-page" v-if="user">
    <NavgationList>
      <template v-slot:header>
        <div class="author-page-nav">
          <router-link to="/">主页/</router-link>
        </div>
      </template>
      <template v-slot:listMain>
        <div class="author-page-userinfo">
          <img :src="user.avatar_url" alt="" />
          <span>{{ user.loginname }}</span>
        </div>
        <div class="author-page-userinfo">
          <span>{{ user.score }} 积分</span>
        </div>
        <div class="author-page-userinfo">
          <span>@{{ user.githubUsername }}</span>
        </div>
        <div class="author-page-userinfo">
          <span>注册时间 {{ user.create_at | formatDate }}</span>
        </div>
      </template>
    </NavgationList>
    <NavgationList>
      <template v-slot:header>
        <div class="author-page-nav">最近参与的话题</div>
      </template>
      <template v-slot:listMain>
        <div v-if="user.recent_replies.length">
          <template v-for="item in user.recent_replies">
            <ArticleList :key="item.id" :item="item" />
          </template>
        </div>
        <div v-else>
          <span>没有最近参与的话题</span>
        </div>
      </template>
    </NavgationList>
    <NavgationList>
      <template v-slot:header>
        <div class="author-page-nav">最近创建的话题</div>
      </template>
      <template v-slot:listMain>
        <div v-if="user.recent_topics.length">
          <template v-for="item in user.recent_topics">
            <ArticleList :key="item.id" :item="item" />
          </template>
        </div>
        <div v-else>
          <span>最近没有创建话题</span>
        </div>
      </template>
    </NavgationList>
  </div>
</template>

<script>
import ArticleList from "../components/ArticleList.vue";
import NavgationList from "../components/NavgationList.vue";
export default {
  components: { NavgationList, ArticleList },
  name: "AuthorPage",
  async created() {
    if (!this.user) {
      const loginname = this.$route.query.loginname;
      await this.$store.dispatch("setCommentUserinfo", loginname);
      console.log(this.user);
    }
  },
  computed: {
    user() {
      return this.$store.state.comment_userinfo;
    },
  },
};
</script>

<style lang='less'>
.author-page {
  width: 1000px;
  margin-left: 170px;
  .author-page-nav {
    padding-left: 20px;
    width: 100%;
    height: 45px;
    line-height: 45px;
    background-color: #918e8e;
    border-bottom: 1px solid #fff;
    a {
      font-weight: 600;
      color: rgb(22, 9, 92);
    }
  }
  .author-page-userinfo {
    padding: 10px;
    display: flex;
    justify-content: left;
    padding-left: 20px;
    align-items: center;
    img {
      width: 40px;
      height: 40px;
      margin-right: 10px;
    }
  }
}
</style>