import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Main from '../components/Main.vue'
import store from '../store/index'
import Login from '../views/Login.vue'
import Context from '../views/Context.vue'
import AuthorPage from '../views/AuthorPage.vue'
import TextEditing from '../views/TextEditing.vue'

Vue.use(VueRouter)

const routes = [
  {
    path:'',
    redirect:'/all'
  },
  {
    path:'/login',
    component:Login
  },
  {
    path:'/context',
    component:Context,
  },
  {
    path:'/authorpage',
    component:AuthorPage
  },
  {
    path:'/textediting',
    component:TextEditing
  },
  {
    path: '/',
    name: 'Home',
    component: Home,
    children:[{
      path:'/:mainpath',
      component:Main,
      beforeEnter: (to, from, next) => {
        store.state.Topices.topice=[]   
        store.commit('removeCommentUserinfo')
        next();
      }
    }],
 
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
// 需要注意的是我们在页面中使用push方法跳转页面，push中包含了query对象，即使我们在路由表中的路径后没有写该query对象对应的参数，跳转之后依然会显示在浏览器路径中。但是在这里我一开始写了context后面跟着？id但是在push跳转中便被匹配到了动态路由。我把路由表中的？id删掉之后，通过` this.$router.push( {path:'/context',query:{id:id}})`命令进行跳转成功了，而且浏览器导航来中有？id的参数。query在目标路由中通过`this.$route.query.name`来调用query传递的参数。