import Vue from 'vue'
import Vuex from 'vuex'
import Topices from './modules/Topics'
import axios from '../plugins/axios'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    userInfo:null,//存放当前网站是否登录，登陆后的个人信息
    comment_context:null,//存放点击的文章详细信息
    comment_userinfo:null//存放点击的文章详细的作者的信息
  },
  mutations: {
    // 请求token，登录使用
  getToken: async (state,userinfo) =>{
        state.userInfo=userinfo
    },
    // 请求文章详细
  setComment:async(state,context)=>{
   state.comment_context=context;
  },
  // 清空文章详细对象
  removeComment:(state)=>{
  state.comment_context=null
  },
  // 请求文章作者详细
  setCommentUserinfo:async(state,commenteUserinfo)=>{
   state.comment_userinfo=commenteUserinfo
  },
  // 移除文章作者详细。
  removeCommentUserinfo:(state)=>{
    state.comment_userinfo=null
  }
  },
  actions: {
    // 请求token的actions
    getToken:async (contest,userinfo)=>{
      await axios.post("/accesstoken",{accesstoken:userinfo}).then((res)=>{
        contest.commit('getToken',res)
      }).catch((err)=>{
        console.log(err);
      })},
      // 请求文章作者详细的方法
      setCommentUserinfo:async(contest,loginname) =>{
        const res= await axios.get(`/user/${loginname}`);
        contest.commit("setCommentUserinfo",res.data)
      },
      // 请求文章详细的方法
    setComment: async(contest,context_id) =>{
      const token =sessionStorage.getItem("isLogin")
      if(token){
        const res=await axios.get(`/topic/${context_id}`,{accesstoken : token});
        contest.commit("setComment",res.data)
        const res1=await axios.get(`/user/${res.data.author.loginname}`)
        contest.commit("setCommentUserinfo",res1.data)
      }else{
        const res=await axios.get(`/topic/${context_id}`);
        contest.commit("setComment",res.data)
        const res1=await axios.get(`/user/${res.data.author.loginname}`)
        contest.commit("setCommentUserinfo",res1.data)
      }
    }
  },
  modules: {
    Topices
  }
})
